<template>
  <div id="home">
    <div class="row">
    <div class="col s12 m12">
      <div class="card green">
        <div class="card-content white-text">
          <p>
            <i class="fa fa-life-ring fa-lg" aria-hidden="true"></i> Publica tu aviso en internet y llega a más personas. Acercate a uno de nuestros locales afiliados. 
            <router-link to="/partner"><span class="chip white green-text right"> Ver locales afiliados</span></router-link>
          </p><br>
        </div>
      </div>
    </div>
    </div>
    <div>
      <ul class="collection with-header">
        <li v-for="ad in ads" v-bind:key="ad.id" class="collection-item">
          <router-link v-bind:to="{name:'view-ad', params: {ad_id: ad.ad_id}}"><div class="chip orange white-text">{{ad.category}}</div></router-link>
          <div class="right">
            {{ad.date}}
            <router-link v-bind:to="{name:'view-ad', params: {ad_id: ad.ad_id}}"><i class="fa fa-chevron-right fa-lg"></i></router-link><br>
          </div>
          <div class="ad">{{ad.ad}}</div>
        </li>
      </ul>
      <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large green"><i class="fa fa-plus"></i></router-link>
    </div>
    </div>
  </div>
</template>

<script>
//import db from "./firebaseInit";
import firebase,{ firestore } from 'firebase';
import moment from 'moment';

moment.locale('es');

export default {
  name: 'home',
  data(){
    return{
      ads: []
    }
  },
  created(){
    firestore().collection('ads').orderBy('date','desc').get()
    //db.collection('ads').orderBy('date','desc').get()
    //db.collection('ads').orderBy('date','desc').get()
      .then(querySnapshot => {
        this.loading = false;
          querySnapshot.forEach(doc => {
            const data = {
              ad_id: doc.data().ad_id,
              category: doc.data().category,
              ad: doc.data().ad,
              cellphone: doc.data().cellphone,
              date: moment(doc.data().date).fromNow()
            };
            this.ads.push(data);
          });
      });
  }
}
</script>
<style scoped>
@import url('https://fonts.googleapis.com/css?family=Ubuntu');

body{
  font-family: 'Ubuntu', sans-serif;
}

.fa-chevron-right{
  color: #ff9800;
}
.ad{
  display: block;
  display: -webkit-box;
  height: 14px * 1.4 * 3;
  font-size: 14px;
  line-height: 1.4;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>




